<template>
    <div class="container">
       <div class="main__cars" v-if="users">
        <div class="main__cars-item" v-for="user in users" :key="user.id">
            <router-link :to="/users/ + user.id" >{{ user.name }}</router-link>
            <p>Email: {{ user.email }}</p>
            <p>City: {{ user.address.city }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                users: null,
            }
        },
        created() {
            this.getUsers()
        },
        methods: {
            async getUsers() {
                let res = await fetch('https://jsonplaceholder.typicode.com/users')
                let data = await res.json()
                this.users = data
            }
        },
    }
</script>

